<template>
	<div id="order-index">
		<public-header title="核销订单"></public-header>
		<div class="content">
			<div class="wrap-search">
				<div class="tab-group flex-lr border-1px-b">
					<div class="tab-item" :class="{active: filter.items.status == -1}" @click="onToggleStatus(-1)">全部</div>
					<div class="tab-item" :class="{active: filter.items.status == 1}" @click="onToggleStatus(1)">待付款</div>
					<div class="tab-item" :class="{active: filter.items.status == 3}" @click="onToggleStatus(3)">待使用</div>
					<div class="tab-item" :class="{active: filter.items.status == 5}" @click="onToggleStatus(5)">待评价</div>
				</div>
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
				<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="order-list"
				>
					<div class="order-group">
						<div class="order-item" v-for="(item, index) in records.data" :key="item.id">
						<div class="order-title flex-lr van-hairline--bottom">
							<div class="flex">
								<div class="number">#{{item.goods_id}}</div>
								<div class="itag" :class="item.order_type_css">{{item.order_type_cn}}</div>
							</div>
							<div class="font-14 c-danger">{{item.status_cn}}</div>
						</div>
						<div class="order-goods van-hairline--bottom">
							<div class="flex-lr">
								<div class="flex goods-info">
									<div class="goods-img">
										<img class="img-100" :src="item.store.logo" alt="">
									</div>
									<div class="goods-name font-14">{{item.goods.name}}</div>
									<div class="font-14 c-gray">x{{item.num}}</div>
								</div>
								<div class="c-danger font-14">￥{{item.price}}</div>
							</div>
							<div class="flex-lr padding-10-t">
								<div class="flex">
									<div class="font-15">{{item.username}}</div>
									<div class="font-14 c-gray padding-10-lr">{{item.mobile}}</div>
									<div class="icon icon-phone2 c-info font-16" @click="util.jsUrl('tel:' + item.mobile)"></div>
								</div>
								<div class="font-14 c-info">{{item.pay_type_cn}}</div>
							</div>
						</div>
						<div class="padding-10">
							<div class="flex font-14 c-gray">
								<div>订单编号：</div>
								<div>{{item.ordersn}}</div>
							</div>
							<div class="flex font-14 c-gray order-time">
								<div>支付时间：</div>
								<div>{{item.paytime_cn}}</div>
							</div>
							<div class="flex font-14 c-danger order-time">
								<div>截止时间：</div>
								<div>{{util.formatDate(item.overtime,'datetime')}}</div>
							</div>
						</div>
					</div>
					</div>
					<div class="loaded" v-if="records.finished">
						<div class="loaded-tips">没有更多了</div>
					</div>
				</van-list>
				<div class="no-data" v-else>
					<img src="static/img/order_no_con.png" alt="">
					<p>暂无数据!</p>
				</div>
			</van-pull-refresh>
			<div class="btn-group">
				<div class="btn-item bg-info">核销码核销</div>
				<div class="btn-item bg-danger">扫码核销</div>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				filter: {
					items: {
						status: -1
					}
				},
				isRefresh: false,
				showPreLoading: true
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'manage/gohome/order/list',
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
			},
			onToggleStatus: function(value) {
				value = parseInt(value);
				if(this.filter.items.status == value) {
					return;
				}
				this.filter.items.status = value;
			},
		},
		mounted(){
			this.onLoad();
		},
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			},
		},
	}
</script>

<style>
	#order-index .content .order-item{
		margin: 10px;
		background-color: #fff;
		border-radius: 5px;
	}
	#order-index .content .order-item .order-title{
		padding: 10px 0;
		margin: 0 10px;
	}
	#order-index .content .order-item .order-title .number{
		font-size: 18px;
		font-weight: 600;
	}
	#order-index .content .order-item .order-title .itag{
		font-size: 12px;
		margin-left: 10px;
		border-radius: 0px;
	}
	#order-index .content .order-item .order-title .label{
		color: #fff;
	}
	#order-index .content .order-item .order-title .label.label-danger{
		background-color: #FF2C4C;
	}
	#order-index .content .order-item .order-title .label.label-info{
		background-color: #33aafc;
	}
	#order-index .content .order-item .order-title .label.label-warning{
		background-color: #f0ad4e;
	}
	#order-index .content .order-group .order-item:last-child{
		margin-bottom: 46px;
	}
	#order-index .content .order-item .order-goods{
		padding: 10px 0;
		margin: 0 10px;
	}
	#order-index .content .order-item .order-goods .goods-info{
		max-width: 75%;
	}
	#order-index .content .order-item .order-goods .goods-img{
		width: 35px;
		height: 35px;
		margin-right: 10px;
	}
	#order-index .content .order-item .order-goods .goods-name{
		max-width: 70%;
		margin-right: 10px;
	}
	#order-index .content .order-item .order-time{
		padding-top: 5px;
	}
	#order-index .content .btn-group{
		position: fixed;
		bottom: 0;
		right: 0;
		left: 0;
		background-color: #fff;
		display: flex;
		flex-wrap: nowrap;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		padding: 10px;
	}
	#order-index .content .btn-group .btn-item{
		min-width: 45%;
		text-align: center;
		padding: 10px 8px;
		border: 1px solid #e5e5e5;
		border-radius: 3px;
		font-size: 14px;
	}
</style>


